<template>
  <div class="shop-box" :style="backImage">
    <div class="shop-container">
      <div class="shop-title">
        <div class="shop-title-icon">
          <Icon type="fireball"></Icon>
          <i class="fa fa-fire"></i>
        </div>
        <div class="shop-title-content" >
          <img :src="shopIntro.shop_info.avatar" />
          <router-link to="/merchant"><font size="69px" color="#2b85e4">{{ shopIntro.shop_info.nickname }}</font></router-link>
          <p style="margin-left:130px;"><router-link to="/merchant">{{ shopIntro.shop_info.shop_content }}</router-link></p>
        </div>
      </div>
      <div class="shop-another-item">
        <div class="shop-another-item-detail" v-for="(item, index) in shopIntro.shop_goods" :key="index">
          <div class="shop-another-item-img">
            <img :src="item.goods_image" alt="">
          </div>
          <div class="shop-anoter-item-intro">
            <p>{{index + 1}}</p>
            <p>{{ item.goods_name }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from '@/vuex/store';
import { mapState } from 'vuex';
import { getShopGoods } from '../../api/index';
export default {
  name: 'ShopHeader',
  computed: {
    ...mapState(['userInfo', 'adminId']),
    backImage () {
      let b = `background-image: url("${this.shopIntro.shop_info.shop_image}")`;
      return b;
    }
  },
  mounted () {
    getShopGoods({admin_id: this.adminId}).then(
      res => {
        if (res.code !== 1) {
          this.$Message.error(res.msg);
        } else {
          this.shopIntro = res.data;
        }
      }

    );
  },
  data () {
    return {
      shopIntro: {
        shop_goods: [
          {
            admin_id: 1,
            goods_image: 'http://img.bennyweb.top/uploads/20181023/Fhci4P204XRb_fSof9AJK4W6PRmP.jpg',
            goods_name: '商品1',
            id: 3
          }
        ],
        shop_info: {
          nickname: '',
          avatar: '',
          admin_id: 1,
          create_time: 0,
          id: 1,
          shop_content: '商铺描述',
          shop_image: 'http://img.bennyweb.top/shop_image.jpg',
          shop_intro: '商铺简介',
          shop_mobile: 1366666778,
          shop_no: '001',
          update_time: 0
        }
      }
    };
  },
  store
};
</script>

<style scoped>
/* 店铺介绍 */
.shop-box {
  width: 100%;
  height: 120px;
  background-color: #484848;
}

.shop-container {
  width: 80%;
  height: 100%;
  margin: 0px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}

.shop-title {
  display: flex;
  flex-direction: row;
}

.shop-title-icon {
  font-size: 46px;
}

.shop-title-content {
  padding-top: 8px;
  margin-left: 15px;
}

.shop-title-content p {
  line-height: 26px;
  font-size: 20px;
}

.shop-title-content a {
  color: #fff;
}

.shop-another-item {
  display: flex;
  flex-direction: row;
}

.shop-another-item-detail {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 15px;
}

.shop-another-item-img {
  height: 80px;
  border-radius: 40px;
  overflow: hidden;
}

.shop-another-item-img img {
  width: 80px;
}

.shop-anoter-item-intro {
  margin-left: 15px;
}
</style>



